<template>
<link rel="preconnect" href="https://fonts.gstatic.com">
<link href="https://fonts.googleapis.com/css2?family=Carter+One&display=swap" rel="stylesheet">

  <div class="selection-div">
    <h1 class="title">Choose a level</h1>
    
    <!-- Listado de niveles + botones en forma de circulo -->
    <div class="container">

      <!-- Mostrar los niveles en forma de tarjeta -->
      <div class="slideshow-container">
        <div v-for="(level, index) in levels" :key="index">

            <!-- Si el nivel esta activo -->
            <div v-if="level.isActive" class="level-container">
              <!-- Siguiente nivel a la derecha -->
              <div v-if="index >= 0 && (index+1) < levels.length" class="card-right fade">
                <div class="numbertext numbertext-right">{{ levels[index+1].id + 1 }} / {{ levels.length }}</div>
                <img class="image-right" :src="require(`@/${levels[index+1].adress}`)" > <!--style="width:100%"-->
                <div class="text text-right">{{ levels[index+1].description }}</div>
              </div>

              <!-- Nivel anterior a la izquierda -->
              <div v-if="index < levels.length && (index-1) >= 0" class="card-left fade">
                <div class="numbertext numbertext-left">{{ levels[index-1].id + 1 }} / {{ levels.length }}</div>
                <img class="image-left" :src="require(`@/${levels[index-1].adress}`)" > <!--style="width:100%"-->
                <div class="text text-left">{{ levels[index-1].description }}</div>
              </div>

              <!-- Nivel actual en el centro -->
              <div class="card fade">
                <div class="numbertext">{{ level.id + 1 }} / {{ levels.length }}</div>
                <img class="image" :src="require(`@/${level.adress}`)" > <!--style="width:100%"-->
                <div class="text">{{ level.description }}</div>
              </div>
              
              <!-- Botones para ir al nivel anterior o al siguiente -->
              <a v-if="index > 0" class="btn-prev" @click="showNextLevel(-1, index)">&#10094;</a>
              <a v-if="index < (levels.length - 1)" class="btn-next" @click="showNextLevel(1, index)">&#10095;</a>
          </div>

        </div>  
      </div>
      
      <br>

      <!-- Botones en forma de circulo -->
      <div class="dots">
        <span v-for="(level, index) in levels" :key="index" style="text-align:center">
          <!-- Circulo del nivel activo sera distinto al resto -->
          <span v-if="level.isActive">
            <span class="dot dot-active" @click="showLevel(index)"></span>
          </span>
          <span v-else>
            <span class="dot" @click="showLevel(index)"></span>
          </span>
        </span>
      </div>
    </div>
    
    <button class="btn-choose" @click="levelIsSelected(current_level)">Choose Character</button>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { STAGES as stages_constants, LEVELS } from '@/consts.js';

export default {
    name: 'LevelSelection',
    components: {
    },
    emits: ['levelIsSelected'], //lo que esta emitiendo a otros componentes
    data() {
        return {
            levelID: '',
            slideIndex: 0,
            current_level: 0,
            levels: LEVELS
        }
    },
    computed: {
        ...mapState( ['stage'] )
    },
    methods: {
        setLevelID(id) {
            this.levelID = id;
        },
        levelIsSelected(id) {
            this.$emit('levelIsSelected', id);
        },
        showNextLevel(n, index) {
          //comprobar que estemos dentro del rango de niveles
          let target = this.current_level + n;
          if ( target >= 0 && target < this.levels.length ) {
            this.current_level = target;
            //desactivar actual
            this.levels[index].isActive = false;
            //activar nuevo nivel
            this.levels[this.current_level].isActive = true;
          }            
        },
        showLevel(index) {
          this.current_level = index;
          for ( let i = 0; i < this.levels.length; i++ ) {
            this.levels[i].isActive = false;
          }
          this.levels[this.current_level].isActive = true;
        }
    },
    created() {
      this.current_level = 0;
    }
}
</script>

<style scoped>
.selection-div {
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /*width: calc(100% - 20px);*/
  /*min-height: calc(100vh - 20px);*/
  background: radial-gradient(#ddd121, #867215);
  font-family: 'Carter One', arial;
  font-size: 1rem;
  font-weight: normal;
}

.title {
  transform: translate(0, -80%);
}

h1 {
  color: #000000; 
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: chartreuse;
  height: 75vh;
}

.slideshow-container {
  width: 100%;
  position: absolute;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*background: blue;*/
}

/*Contenedor de los niveles*/
.level-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 80%;
  height: auto;
  padding: 0;
  left: 50%;
  top: 10%;
  transform: translate(-50%, 1%);
  /*background-color: #ffffff;*/
  border-radius: 5px;
}

/*Tarjeta del nivel principal*/
.card {
  background: red;
  padding: 10px;
  position: relative;
  display: inline-block;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
  max-width: 300px;
  max-height: 500px;
  border-radius: 10px;
}

.card:after {
  padding-top: 56.25%;
  /* 16:9 ratio */
  display: block;
  content: '';
}

.image {
  border-radius: 10px;
  object-fit: cover;
  max-width: 100%;
  height: auto;
  position: relative;
  display: inline-block;
}

/*Botones para cambiar de nivel*/
.btn-prev, .btn-next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: auto;
  padding: 20px;

  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 10px;
  user-select: none;
  background-color: rgba(209, 198, 198, 0.8);
}

/*Colocar cada boton a un extremo del contenedor al que pertenecen*/
.btn-next {
  right: 0;
}

.btn-prev {
  left: 0;
}

/*Efectos hover sobre los botones*/
.btn-prev:hover, .btn-next:hover {
  background-color: rgba(0,0,0,0.8);
}

/*Texto de la tarjeta del nivel*/
.text {
  color: #000000;
  font-size: 1.5vmax;
  padding: 8px 12px;
  position: absolute;
  bottom: 4%;
  width: 100%;
  text-align: center;
  display: inline-block;
  transform: translate(-50%, -50%);
  left: 50%;
}

/*Numero del nivel (1/3 etc)*/
.numbertext {
  color: #000000;
  font-size: 1vmax;
  padding: 10px 12px;
  position: absolute;
  transform: translateY(50%);
  display: inline-block;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 65%;
}

/**Nivel a la derecha**/
.card-right {
  position: absolute;
  left: 70%;
  top: 50%;
  width: auto;
  height: 80%;
  transform: translate(-50%,-50%);
  background: rgb(238, 255, 0);
  filter: blur(3px);
  padding: 10px;
  display: inline-block;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
  
.image-right {
  height: 50%;
}

.text-right {
  font-size: 1vmax;
}

.numbertext-right {
  font-size: 0.5vmax;
}
/**********************/

/**Nivel a la izquierda**/
.card-left {
  position: absolute;
  left: 30%;
  top: 50%;
  width: auto;
  height: 80%;
  transform: translate(-50%,-50%);
  background: rgb(94, 255, 0);
  filter: blur(3px);
  padding: 10px;
  display: inline-block;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.image-left {
  height: 50%;
}

.text-left {
  font-size: 1vmax;
}

.numbertext-left {
  font-size: 0.5vmax;
}
/**********************/

/*Contenedor botones en formato circular*/
.dots {
  position: absolute;
  bottom: 5%;
}
/*Botones individuales*/
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 4px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.dot:hover {
  background-color: #ffffff;
}
/*Boton del nivel activo*/
.dot-active {
  background-color: #ffffff;
  height: 20px;
  width: 20px;
  margin: -2.5px 2px;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: .15s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.btn-choose {
  position: absolute;
  top: 93%;
  left: 90%;
  transform: translate(-70%, -50%);

  background-color: #00ffff;
  padding: 10px;
  border-radius: 10px;
  outline: none;
  border: none;
  text-decoration: none;
  color: #000000;
  padding: 1%;
  margin-top: 10px;
  font-size: 1.2rem;
  cursor: pointer;
}
</style>